import { CLOUD_URL_PREFIX, dateFormat } from '../utils';
import COS from 'cos-js-sdk-v5';
import axios from 'axios';
var cos = new COS({
  SecretId: 'AKIDiEUTDi7zlHXY73Ia8RVKthFYgYAZpaib',
  SecretKey: 'pQwDF7lALK4j9LH4f6JHAoEOIxOdcEiy',
});
export function getArticleList() {
  return new Promise((resolve, reject) => {
    try {
      axios.get(`${CLOUD_URL_PREFIX}/listData.js`).then((res) => {
        resolve(res.data.list);
      }, (error) => {
        console.log(error);
        reject();
      })
    } catch (error) {
      console.log(error);
      reject();
    }
  });
}
export function submitFeedback(feedBack: string) {
  return new Promise((resolve, reject) => {
    let cur = new Date();
    let key = `feedBack/${dateFormat(`YYYY-mm-dd HH:MM`, cur)}-${cur.getTime()}.txt`
    cos.putObject({
      Bucket: 'douban-1300076408',
      Region: 'ap-guangzhou',
      Key: key,
      Body: feedBack,
    }, function (err: any, data: any) {
      console.log(err || data);
      if (data) {
        resolve(data);
      } else {
        reject(err);
      }
    });
  });
}



